/**
 * Image and video styles.
 */

.thumbnail {
  .shade;
  padding: 0;
}

.no-resize img,
img.no-resize {
  max-width: none;
  margin-left: 0;
  width: auto;
}

.caption {
  margin-bottom: 10px;
}

img + .caption {
  & > h1, h2, h3, h4, h5, h6 {
    &:first-child {
      padding-top: 0;
      margin-top: 11px;
    }
    &:last-child {
      margin-bottom: 0;
      padding-bottom: 0;
    }
  }
}

/* Deprecated.  Use embed-responsive instead */
.container-video {
  height: 0;
  margin: 0 0 6px;
  overflow: hidden;
  padding-bottom: 56.25%;
  padding-top: 30px;
  position: relative;
}
.container-video iframe,
.container-video object,
.container-video embed {
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

/* TODO: Retire this class for a more generic name */
.keyfacts {
  background: #e8e8e8;
  border: 1px solid #ddd;
  margin: 20px 0 40px;
  padding: 0 30px 30px;
}
