/*
 * Page footer
 */

.footer-discover {
  background:        @footer-discover-bg;
  h3 {
    color:           @footer-discover-fg;
    font-size:       1.8em;
    margin-bottom:   @line-height-computed;
    text-transform:  uppercase;
    font-family:     @font-family-serif;
    font-weight:     400;
  }
}

.footer {
  color:             @footer-fg;
  font-size:         1em;
  background:        @footer-bg;
  background:        linear-gradient(to bottom, @footer-bg 0%, @footer-discover-bg 100%) repeat scroll 0 0;
  border-top:        none;
  padding-top:       16px;
  padding-bottom:    16px;
  // Source Sans Pro Black (weight 900) is not currently loaded.
  //  h3 {
  //    font-family: 'Source Sans Pro', sans-serif;
  //    font-size: 1em;
  //    letter-spacing: 0.01em;
  //    text-transform: uppercase;
  //    a, a:visited {font-weight: 900; color: #fff;}
  //  }
  ul {
    padding-left:    0;
    list-style:      none;
    line-height:     @line-height-base;
    margin-bottom:   (@line-height-computed / 2);
  }
  a:not(.btn) {
    &, &:visited {
      color:         @footer-fg;
    }
    &:hover, &:focus {
      color:         @footer-hover;
    }
  }
  .btn {
    margin-top: 1.0em;
    margin-bottom: 1.0em;
  }
  .row {
    margin-bottom: 10px;
  }

  .pull-right {
    @media (max-width: @screen-xs-max) {
      float: none !important;
    }
  }
}

.container-footer-copyright {
  background:         @footer-panel-light;
  color:              @footer-panel-dark;
  font-size:          0.8em;
  padding-top:        @line-height-computed;
  padding-bottom:     @line-height-computed;
  a {
    color:            @footer-panel-dark;
  }
}
.footer-login-link {
  font-size: 0.8em;
  color:              @footer-panel-dark;
  &:hover,
  &:active,
  &:focus {
    text-decoration:  none;
  }
}


/* Subsite footer */
.footer-affiliations {
  .topline;
  background-color: @footer-panel-light;
  padding:            30px 0 16px;
}
.footer-affiliation-image {
  padding:            0 0 16px 30px;
  float:              left;
  img {
    max-height:       75px;
    width:            auto;
  }
}

.footer-subsite-content {
  background-color:    @footer-panel-dark;
  color:               @footer-panel-light;
  padding:             30px 0 0;
  .row > div {
    margin-bottom:     16px;
  }
  h3 {
    color: @footer-panel-light;
    margin-top:        0;
    font-weight:       400;
  }
  ul {
    line-height:       @line-height-base;
  }
  a:not(.btn) {
    color:             @footer-panel-light;
    font-size:         1.2em;
    font-weight:       300;
    &:hover {
      opacity: 0.75;
    }
  }
}
