/*
 * EdGEL Panels
 * --------------------------------------------------
 */

/* High visual impact panel style */
.panel-uoe-high {
  .panel-variant(@brand-color; @brand-bg-text; @brand-color; @brand-color);
  background-color: @brand-color;
  color: @brand-bg-text;

  .panel-heading {
    .box-shadow(none);
    padding-bottom: 0;
  }
  .panel-title {
    margin-top: 10px;
    margin-bottom: 0;
  }

  a {
    color: @brand-link-color;
    &:hover {
      color: darken(@brand-link-color, 5%);
    }
  }
}

/* Medium visual impact panel style */
.panel-uoe-medium {
  .panel-variant(@panel-default-border; @brand-bg-text; @brand-color; @brand-color);

  .panel-heading a {
    color: @brand-link-color;
    &:hover {
      color: darken(@brand-link-color, 5%);
    }
  }
}

/* Low visual impact panel style */
.panel-uoe-low {
  .panel-variant(@panel-default-border; @panel-default-text; @panel-bg; @brand-color);
  .topline;

  .panel-heading {
    .box-shadow(none);
    padding-bottom: 0;
  }
  .panel-title {
    margin-top: 10px;
    margin-bottom: 0;
  }

  .panel-heading a {
    color: @brand-as-link;
    &:hover {
      color: @brand-as-link-hover;
    }
  }
}
