/*
 * Bootstrap table overrides
 */
.table > thead > tr > th {
  border-bottom: 1px solid #bbb;
}
table th {
  a {
    display: block;
    position: relative;
  }
  &.active {
    a {
      padding: 0 25px 0 0;
    }
    img {
      position: absolute;
      right: 3px;
      top: 3px;
    }
  }
}

.table-striped tr {
  background-color: #fff;
}

td.checkbox,
th.checkbox {
  text-align: center;
  display: table-cell;
  .checkbox {
    margin: 0 0;
  }
}
