/*
 * Bootstrap type overrides
 * --------------------------------------------------
 */

/* Bootstrap lead paragraph style */
.lead {
  font-size: 1.3em;
  font-weight: 400;
}

/* Published date */
.article-published {
  display:    block;
  margin-top: 2em;
}

/* Bootstrap help block */
.help-block {
  color: @text-muted;
  font-size: 12px;
  margin-bottom: 15px;
}

/* Small and cite elements */
small,
.small,
cite {
  font-size:     85%;
  color:         #6a6a6a;
}

/* Links */
a {
  cursor: pointer;

  h1, h2, h3, h4, h5, h6, p, ul, ol {
    color: #428bca;
  }
}

/* Heading font changes */
h5 {
  font-weight: 500;
}
h6 {
  font-weight: 600;
  small {
    font-weight: 400;
  }
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  clear: left;
  small,
  .small {
    font-weight: 300;
  }
}
h3, h4, h5, h6,
.h3, .h4, .h5, .h6 {
  color: #666;
}
h4, .h4 {
  margin-top: 20px;
}

h1 + h2,
h2 + h3,
h3 + h4,
h4 + h5,
h5 + h6 {
  margin-top: -4px;
}

/* H1 as a page header */
.page-header {
  margin:        0 0 10px;
}

/* Horizontal rule */
hr {
  display:       block;
  clear:         both;
  border-bottom: 1px solid #ccc;
  margin:        @grid-gutter-width 0;
}

/* Legend element */
legend a:focus {
  outline:       initial;
}

legend {
  font-size:     1.65em;
}

/* Remove extra bottom margin in panels etc */
p:last-child,
dl:last-child,
ol:last-child,
ul:last-child {
  margin-bottom: 0;
}
