/*
 * Admin Interface Header
 */

.container-admin-bar {
  padding-bottom: 10px;
  h1, h2, h3, h4, h5, h6 {
    font-family: @font-family-serif;
    font-size: 2em;
    font-weight: 400;
    margin-top: 15px;
  }
}

/* Primary task links in admin bar */
.admin-bar-primary {
  padding-bottom: 0.5rem;
  border-bottom: solid #fff 1px;
  li {
    line-height: 2.7;
  }
}

/* Secondary task links in admin bar */
.admin-bar-secondary {
  float: right;
  li {
    line-height: 2.1;
  }
}
