/*
 * Bootstrap Navbar Overrides
 * --------------------------------------------------
 */

.navbar-nav {
  margin-top: 0;
  margin-bottom: 0;
}

.navbar-default {
  border-width: 0;
  .navbar-nav > li {
    background-color: @navbar-default-link-color-bg;
    border-left: 1px solid @navbar-default-border;
    &:last-of-type {
      border-right: 1px solid @navbar-default-border;
    }
    a {
      @media (max-width: @grid-float-breakpoint-max) {
        padding: 1em 1.6em;
      }
      line-height: 10px;
      @media (min-width: @grid-float-breakpoint) {
        line-height: 20px;
      }
    }
  }
}

/* Add link underline on navbar dropdowns. */
.dropdown-menu > li > a {
  &:hover,
  &:focus {
    text-decoration: underline;
  }
}

@media (max-width: (@grid-float-breakpoint-max)) {
  .navbar-nav > li {
    border-bottom: 1px solid @navbar-default-border !important;
    .dropdown-menu {
      padding-top: 1px;
    }
  }
}
@media (min-width: @screen-xs-min) and (max-width: (@grid-float-breakpoint-max)) {
  .navbar-default {
    li {
      width: 33.33%;
      float: left;
      li {
        width: 100%;
      }
    }
  }
}
@media (min-width: @screen-xs-min) and (max-width: 550px) {
  .navbar-default {
    li {
      width: 50%;
      li {
        width: 100%;
        float: none;
      }
    }
  }
}
