/**
 * EdGEL Blockquote
 */

blockquote {
  .shade;
  clear: both;
  border-color: @brand;
  background: #fff;
  padding: 20px;
  margin: 0 0 15px;
  font-size: 1.2em;
  line-height: 1.6em;
  font-style: italic;
  quotes: "“" "”" "‘" "’";
}
blockquote + small {
  display: block;
  margin: -20px 0 @grid-gutter-width;
}
blockquote p:first-of-type:before,
blockquote p:last-of-type:after {
  color: #ccc;
  content: open-quote;
  font-size: 3.2em;
  line-height: 1px;
  margin-right: 0.25em;
  vertical-align: -27px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 400;
}
blockquote p:last-of-type:after {
  content: close-quote;
  margin-right: 0;
  margin-left: 0.25em;
}
blockquote footer cite span {
  display: block;
}
