/*
 * EdGEL Breadcrumb trail
 * --------------------------------------------------
 */

/* The breadcrumb ordered list */
.breadcrumb {
  font-size: 0.9em;
  margin-bottom: 0;
  // TODO: Review use of this over @line-height-base (or default)
  line-height: @line-height-computed;
  a, a:visited {
    color: @breadcrumb-color;
    display: inline-block;
  }
  > li {
    + li:before {
      padding: 0 7px;
    }
    &:last-child,
    &:last-child a,
    &:last-child a:visited {
      color: #000;
      cursor: default;
    }
  }
}

/* Breadtrail is a wrapper for the breadcrumb and contact button */
.breadtrail {
  margin: 0 -10px;
  & > div {
    margin-bottom: 20px;
    padding: 0 10px;
  }
  & > div:last-child {
    margin-bottom: 30px;
  }
  .btn {
    // TODO: Review use of this over @line-height-base (or default)
    line-height: @line-height-computed;
    padding-left: 2px;
    padding-right: 2px;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 0.9em;
    border-style: none;
  }
}
