/**
 * Components for news items
 */

/* List of news items */
.list-news {
  list-style:      none;
  margin:          0 0 @grid-gutter-width;
  padding:         0;
  li {
    .clearfix;
    border-top:    1px solid #ccc;
    padding:       0 0 @grid-gutter-width;
    display:       table;
  }
  li:first-child {
    border-top:    none;
  }
  h6 + h6 {
    margin-top:    -5px;
  }
}
