/*
 * EdGEL Mixins
 * --------------------------------------------------
 */

/* Box shadows */
.shade() {
  @shadow: 0 4px 6px -5px rgba(0, 0, 0, 0.3), 0 0 1px rgba(0, 0, 0, 0.1);
  .box-shadow(@shadow)
}

.shade-light() {
  @shadow: 0 2px 1px -1px rgba(0, 0, 0, 0.15);
  .box-shadow(@shadow)
}

/* Top line border */
.topline() {
  border-top: 8px solid @brand-color;
}
