/*
 * Overview panels
 * --------------------------------------------------
 */

/* Common overview panel styles */
.panel-overview,
.panel-overview-medium,
.panel-overview-high {
  .panel-variant(@panel-default-border; @panel-default-text; @panel-bg; @brand-color);

  .panel-heading {
    box-shadow: none;
  }

  /* Reset the panel-title margins */
  .panel-title {
    margin-top: 5px;
    margin-bottom: 5px;
  }
}

/* Medium and default visual impact overview panel */
.panel-overview,
.panel-overview-medium {
  .panel-title {
    color: @brand-as-link;
  }

  > a,
  .panel-heading a {
    color: @brand-as-link;

    &:hover,
    &:focus {
      color: @brand-as-link-hover;
    }
  }
}

/* Add the topline as an inset border so title text positioning is consistent */
.panel-overview-medium {
  @shadow: 0 4px 6px -5px rgba(0, 0, 0, 0.3), 0 0 1px rgba(0, 0, 0, 0.1), inset 0 7px 0 @brand;
  .box-shadow(@shadow);
}

/* High visual impact overview panel */
.panel-overview-high {
  .panel-variant(@panel-default-border; @brand-bg-text; @brand-color; @brand-color);

  a > .panel-heading {
    background: @brand-color;
  }
  .panel-heading:hover {
      background: @brand-as-link-hover;
  }

  > a,
  .panel-heading a {
    color: @brand-bg-text;
  }
}
