/**
 * Miscellaneous spacing.
 */

/* Spacing above overview panel grid */
p + .row {
  margin-top: @grid-gutter-width;
}

/* Forms */
td .form-group {
  margin: 0 0;
  * {
    margin-bottom: 0;
  }
}

.form-file {
  margin-top:    0;
  margin-bottom: 10px;
  padding-top:   0;
}

.form-inline .panel-body {
  display: block;
  padding: 15px 15px;
}

.form-group + .help-block {
  margin-top: -10px;
}
@media screen and (min-width: @screen-sm-min) {
  .form-horizontal .help-block {
    margin-left: 25%;
    padding-left: 5px;
  }
}

.alert {
  margin-top: @line-height-computed;
}
.alert > .glyphicon {
  float: left;
  padding-right: 1em;
}

.modal-body {
  overflow: auto;
  p:last-child {
    margin-bottom: 0;
  }
}

.container > p:last-child {
  margin-bottom: 0;
}
