/**
 * Modify Bootstrap pagination lists.
 */

/* Add the EdGEL standard box shadow to component and increase spacing. */
.pagination {
  .shade;
  margin:           @grid-gutter-width 0;
  background-color: @panel-bg;
}

/* Only have a solid border between list items. */
.pagination {
  > li > a,
  > li > span {
    border-style: none none none solid;
  }
  > li:first-of-type > a,
  > li:first-of-type > span {
    border-left-style: none;
  }
}

/* Remove rounded corners from pagination element, consistent with panels. */
.pagination {
  border-radius: 0;
}

.pagination-sm,
.pagination,
.pagination-lg {
  > li:first-child {
    & > a,
    & > span {
      border-top-left-radius:    0;
      border-bottom-left-radius: 0;
    }
  }
  > li:last-child {
    & > a,
    & > span {
      border-top-right-radius:    0;
      border-bottom-right-radius: 0;
    }
  }
}

/* We have a slightly larger horizontal padding on the large pagination to make
   it more square. */
.pagination-lg {
  > li {
    > a,
    > span {
      padding: @padding-large-vertical 19px;
    }
  }
}

/* If we've not wrapped the glyphicon span in another span for the first/last
   pagination item, fix the 1px offset */
.pagination > li > .glyphicon {
  top:            0;
  padding-top:    9px;
  padding-bottom: 11px;
}

/* The pagination-condensed style when used in conjunction with the pagination
   size classes */
.pagination.pagination-condensed {
  > li {
    > a,
    > span {
      padding: @padding-base-vertical 5px;
      @media (min-width: 1200px) {
        padding: @padding-base-vertical 6px;
      }
    }
  }
}

.pagination-lg.pagination-condensed {
  > li {
    > a,
    > span {
      padding: @padding-large-vertical 6px;
      @media (min-width: 1200px) {
        padding: @padding-large-vertical 8px;
      }
    }
  }
}

.pagination-sm.pagination-condensed {
  > li {
    > a,
    > span {
      padding: @padding-small-vertical 4px;
      @media (min-width: 1200px) {
        padding: @padding-small-vertical 5px;
      }
    }
  }
}
