/**
 * Unfortunately Twitter's Typeahead breaks the layout by adding style attributes
 * to the input.  Here we do nasty things to override that and style everything else
 * according to Bootstrap.
 */

.twitter-typeahead {
  width:        100%;

  .dropdown-item {
    border:     1px solid transparent;

    &:extend(.dropdown-menu > li > a);

    &.active,
    &:hover{
      &:extend(.dropdown-menu > li > a:hover);
      cursor:   pointer;
    }

    &.active {
      border:   1px dotted @text-color;
    }
  }

  /* Overrides for default input-group to reinstate Bootstrap styling */
  .input-group & {
    display:           block !important;
    height:            @input-height-base;
    .dropdown-menu {
      top:             @input-height-base !important;
    }
    &:not(:first-child):not(:last-child) {
      .form-control {
        border-radius: 0;
      }
    }
    &:first-child {
      .form-control {
        border-top-left-radius:     @input-border-radius;
        border-bottom-left-radius:  @input-border-radius;
        border-top-right-radius:    0;
        border-bottom-right-radius: 0;
      }
    }
    &:last-child {
      .form-control {
        border-top-left-radius:     0;
        border-bottom-left-radius:  0;
        border-top-right-radius:    @input-border-radius;
        border-bottom-right-radius: @input-border-radius;
      }
    }
  }

  /* Overrides for input-group-sm */
  .input-group.input-group-sm & {
    height:     @input-height-small;
    .dropdown-menu {
      top:      @input-height-small !important;
    }
    .form-control {
      &:extend(.input-group-sm > .form-control);
    }
  }
  /* Overrides for input-group-lg */
  .input-group.input-group-lg & {
    height:     @input-height-large;
    .dropdown-menu {
      top:      @input-height-large !important;
    }
    .form-control {
      &:extend(.input-group-lg > .form-control);
    }
  }
  /* Fix positioning of dropdown in heading search box */
  .page-head-search & {
    .dropdown-menu {
      top:      @page-head-input-height !important;
    }
  }

  /* Enforce grey background for the typeahead input (stop it turning white) */
  > input:not([id]) {
    background-color: @input-bg !important;
  }
}
