/*
 * Figure HTML element styling
 * --------------------------------------------------
 */

figure {
  margin-bottom: 15px;
  .shade
}
figure img {
  width: 100%;
  height: auto;
  .shade
}
figcaption {
  color: @descriptive-text-color;
  padding: 11px 20px;
  &:empty {display: none;}
  margin-bottom: 0;
  background: @panel-bg;
  clear: both;
}

figure.pull-left {
  padding-left: 0;
  padding-right: 0;
  margin-right: 20px;
  clear: left;
}
figure.pull-right {
  padding-left: 0;
  padding-right: 0;
  margin-left: 20px;
  clear: right;
}
