/*
 * Progress Bar Steps
 * --------------------------------------------------
 */

/* Modifier to the Bootstrap .progress block */
.progress-stepped {
  margin: 0 7em @line-height-computed 0;
}

/* New component to display the steps below the progress bar */
.progress-steps {
  margin: -@line-height-computed 0 @line-height-computed 0;
  padding: 0 0;
  display: table;
  table-layout: fixed;
  width: 100%;
  box-sizing: border-box;
  list-style: none;
}

/* Step element within progress-steps */
.progress-step {
  &:before {
    content: "\e253"; /* glyphicon-triangle-top */
    font-family: 'Glyphicons Halflings';
    font-style: normal;
    font-weight: normal;
    line-height: 0.8em;
    margin: 0 0.5em 0 -0.5em;
    display: block;
  }
  display: table-cell;
  padding: 0 0.7em 0 0;
  &:last-of-type {
    width: 7em;
    padding-right: 0;
  }
}

/* Progress step element modifiers */
.progress-step-completed {
  color: @disabled-text-color;
}
.progress-step-current {
  font-weight: 600;
}

@media screen and (max-width: @screen-xs-max) {
  .progress-stepped {
    margin-right: 5.5em;
  }
  .progress-step:last-of-type {
    width: 5.5em;
  }
}

/* Component for the "page n of m" text */
.progress-number {
  margin-bottom: (@line-height-computed * 2);
  text-align: center;
  padding: 5px 5px;
  background-color: #ffffff;
}
