/*
 * Bootstrap Panel Overrides
 * --------------------------------------------------
 */

.panel {
  border-style: none;
  .shade;
  &:last-child {
    margin-bottom: 20px;
  }
}
.panel-heading {
  border-style: none;
  .shade-light
}
// TODO: See if we can retire this for a new .img-panel class //
.panel img {
  width: 100%;
  height: auto;
  .shade;
}

.panel-body {
  line-height: @line-height-base;
  position: relative;
}
h1.panel-title { font-size: @font-size-h1; }
h2.panel-title { font-size: @font-size-h2; }
h3.panel-title { font-size: @font-size-h3; }

.panel-default .panel-title {
  font-weight: 600;
  font-size: 17px;
}

.panel > .list-group {
  list-style: none;
  border-style: none;
}
.panel > .list-group .list-group-item {
  border-style: none;
}

/* Panel groups (accordion) overrides */
.panel-group .panel-title {
  font-weight: 600;
}
.panel-group .panel-heading {
  padding-top: 10px;
}
.panel-group .panel-primary > .panel-heading {
  &:hover {
    background-color: darken(@panel-primary-heading-bg, 5%);
  }
  .panel-title, a {
    display: block;
  }
}
.panel-group .panel-default > .panel-heading {
  &:hover {
    background-color: darken(@panel-default-heading-bg, 5%);
  }
  .panel-title, a {
    display: block;
  }
}
.panel-group .panel-heading + .panel-collapse .panel-body {
  border-top: none;
}
.panel-group .panel-default .panel-heading {
  background-color: @panel-default-heading-bg;
}

.panel [data-toggle="collapse"] {
  &:before {
    content: "\e082";
    font-family: 'Glyphicons Halflings';
    vertical-align: sub;
    padding-right: 20px;
    display:inline-block;
  }
  &[aria-expanded="false"],
  &.collapsed,
  &.collapse.in {
    &:before {content: "\e081";}
  }
}

.panel-collapse,
.panel-group .panel-heading .panel-title a {
  padding-left: 40px;
  &[data-toggle="collapse"] {
    padding-left: 0;
  }
}

.panel.panel-group {
  padding: 20px;
  .panel {
    margin-top: 0;
    .box-shadow(none);
  }
  .panel > .panel-heading {
    background: none;
  }
}

.panel figure {
  margin-bottom: 0;
  .box-shadow(none);

  figcaption {
    background-color: @gray-lighter;
  }
}

/* Provide an indented section within a panel-body */
.panel-body-offset {
  padding-left: 0.75em;
  border-left: solid 2px @brand;
  margin-left: 2em;
}
/* Reset bottom margins to ensure red line is vertically centred */
.panel-body > &,
.panel-body-offset > & {
  p:last-child,
  dl:last-child,
  ol:last-child,
  ul:last-child {
    margin-bottom: 0;
  }
}

/* Support vertical alignment of glyphicon before panel-body-offset */
.panel-body-offset-marker {
  position: absolute;
  left: 20px;
  top: 50%;
  margin-top: -0.5em;
}
